import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { ThemeProvider, createTheme } from '@mui/material/styles';

const theme = createTheme({
  palette: {
    primary: {
      main: '#1976d2',    //  (: )
      dark: '#115293',    // 
    },
    secondary: {
      main: '#d32f2f',    //  (: )
    },
  },
  typography: {
    h6: {
      fontWeight: 'bold', // h6
    },
  },
});

ReactDOM.render(
  <ThemeProvider theme={theme}>
    <App />
  </ThemeProvider>,
  document.getElementById('root')
);
